@media (min-width:1440px){
    .summary-side{
        margin-top: 9px;
        margin-bottom: 9px;
        padding: 1rem;
        width: 50%;
    }
    .sum-all-score{
        margin-top: 2rem;
    }
    .score-line{
        display: flex;
    }
    .score{
        display: flex;
    }
    .reaction-score{
        border-radius: 10px;
        background-color: hsl(0, 100%, 67%,0.1);
    }
    .memory-score{
        border-radius: 10px;
        background-color: hsl(39, 100%, 56%,0.1);
    }
    .verbal-score{
        border-radius: 10px;
        background-color: hsl(166, 100%, 37%,0.1);
    }
    .visual-score{
        border-radius: 10px;
        background-color: hsl(234, 85%, 45%,0.1);
    }
    
    ul{
        padding: 0;
    }
    li{
        margin-top: 10px;
        justify-content: space-between;
        display:flex;
        padding: 13px;
    }
    img{
        padding-right: 8px;
    }
    button{
        background: hsl(224, 30%, 27%);
        border: none;
        border-radius: 30px;
        padding: 1rem;
        margin-top: 3.5rem;
        width: 100%;
        cursor: pointer;
    }
    button:hover{
        background: linear-gradient(hsl(252, 100%, 67%),hsl(241, 81%, 54%));
        
    }
}

