*{
  margin: 0;
  padding: 0;
  font-family: 'Hanken Grotesk', sans-serif;
  box-sizing: border-box;
}
@media (min-width:1440px) {
 
  body{
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    min-height: 100vh;
    background: #fafafa;
  }
  .container{
    box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px, rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
    border-radius: 15px;
    display: flex;
    max-width: 38rem;
    max-height: 35rem;
    background: hsl(0, 0%, 100%);
  }
  .font-white{
    color: hsl(0, 0%, 100%);
  }
  .hanken-grotesk{
    font-family: 'Hanken Grotesk', sans-serif;
  }
}
