@media only screen and (min-width:1440px){
    .result-side{
        padding: 1rem;
        background: linear-gradient(hsl(252, 100%, 67%),hsl(241, 81%, 54%));
        width: 50%;
        border-radius: 14px;
    }
    .result-page{
        margin: 9px;
        text-align: center;
    }
    .radius{
        text-align:-webkit-center;
        padding: 1.5rem;
    }
    .radius-display{
        padding-top: 1rem;
        height: 140px;
        width: 140px;
        background:linear-gradient(hsla(256, 72%, 46%, 1),hsla(241, 72%, 46%, 0));
        margin: 2rem;
        border-radius: 100%;
    }
    .comment-line{
        margin-top: 10px;
    }
}


@media only screen and (min-width:375px) {
    .result-side{
        background: linear-gradient(hsl(252, 100%, 67%),hsl(241, 81%, 54%));
        text-align: center;
        /* border-radius: 0 0 30px 30px; */
        border-bottom-left-radius: 14px;
        border-bottom-right-radius: 14px;
    }
    .result-page{
        padding: 1.4rem;
    }
    .radius{
        text-align:-webkit-center;
        padding: 1.5rem;
    }
    .radius-display{
        padding-top: 1rem;
        height: 140px;
        width: 140px;
        background:linear-gradient(hsla(256, 72%, 46%, 1),hsla(241, 72%, 46%, 0));
        margin: 1rem;
        border-radius: 100%;
    }
    .comment-line{
        margin-top: 10px;
        color: hsl(0, 0%, 100%);
    }
}